import {FC} from "react";
import "./BigArticle.scss";
import Category from "../../category";
import FeedInfo from "../../feedInfo";
import ArticleImage from "../../../../images/articleImage.png";
import Tag from "../../tag";
import FeedCard from "../../feedCard";
import MockedVideo from "../../../../images/video.png";
import Quote from "../../quote";
import Share from "../../share";
import Advertisement from "../../advertisement";
import Comments from "../../comments";
import Newsline from "../../newsline";
import Recommended from "../../recommended";

interface BigArticleProps {
  heading: string;
}

const BigArticle: FC<BigArticleProps> = ({heading}) => {
  const mockedTags = ["Политика", "Расследования", "Происшествия", "Счастье во всем мире"];

  return (
    <section className={'bigArticle'}>
      <section className={'bigArticle__header header'}>
        <div className={'header__contentWrapper contentWrapper'}>
          <div className={'contentWrapper__value value'}>
            <Category name={'Общество'}/>
            <h2 className={'value__heading'}>{heading}</h2>
            <div className={'value__stats'}>
              <FeedInfo views={23500} type={'all'} comments={123} readingTime={23} date={'14 ноября 2022'}/>
            </div>
            <div className={'value__tags tags'}>
              {mockedTags.map((tag: string) => {
                return <Tag name={tag}/>
              })}
            </div>
          </div>
        </div>
        <div className={'header__mainImage'}>
          <img src={ArticleImage} alt={'Картинка статьи'}/>
        </div>
      </section>
      <section className={'bigArticle__mainPart mainPart'}>
        <section className={'mainPart__mainContent mainContent'}>
          <section className={'mainContent__article article'}>
            <section className={'article_centered article__text text'}>
              <p className={'text__header'}>
                В Москве во вторник, 30 ноября, ожидаются самые сильные осадки за последние 69 лет. Их принесет в столицу генуэзский циклон «Бенедикт». Об этом сообщил ведущий сотрудник центра погоды «Фобос» Евгений Тишковец.
              </p>
              <p className={'text__subtext'}>
                По данным столичных коммунальщиков, высота снежного покрова в городе увеличится на 2 см. Кроме того, ожидается похолодание, что может поспособствовать образованию гололедицы.
              </p>
            </section>
            <section className={'article_cardAside'}>
              <FeedCard
                type={'half'}
                size={'small'}
                heading={'Федеральные эксперты прокомментировали итоги выборов в Волгоградской области'}/>
              <aside className={'article__text'}>
                <p className={'text__subtext'}>
                  По его словам, выпадет до 23 мм осадков, или 40% месячной нормы, — два ведра воды на квадратный метр.
                </p>
                <p className={'text__subtext'}>
                  Синоптик отметил, что обложные дожди будут лить стеной и сменятся во второй половине дня снегопадом с ухудшением видимости до 900 м.
                </p>
                <p className={'text__subtext'}>
                  «Это будут самые сильные осадки за этот день за последние 69 лет. Рекорд дня пока принадлежит 1952 году (25,4 мм). Суточный рекорд осадков для всего ноября — 41 мм», — приводят слова специалиста «РИА Новости».
                </p>
              </aside>
            </section>
            <section className={'article_centered article__text text'}>
              <p className={'text__subtext'}>
                По данным столичных коммунальщиков, высота снежного покрова в городе увеличится на 2 см. Кроме того, ожидается похолодание, что может поспособствовать образованию гололедицы.
              </p>
              <div className={'text__image'}>
                <img src={MockedVideo} alt={'Видео'}/>
              </div>
            </section>
            <section className={'article_centered article__text text'}>
              <p className={'text__subtext'}>
                Городские службы Москвы переведены в режим повышенной готовности.
              </p>
              <p className={'text__subtext'}>
                По данным столичных коммунальщиков, высота снежного покрова в городе увеличится на 2 см. Кроме того, ожидается похолодание, что может поспособствовать образованию гололедицы.
              </p>
              <p className={'text__subtext'}>
                Накануне главный специалист Московского метеобюро Татьяна Позднякова рассказала, что на текущей неделе в Москве ожидается неустойчивая погода с температурой выше нормы на 2–3 градуса. Во вторник будут дожди, в остальные дни — снег. Кроме того, начиная со вторника, резко понизится атмосферное давление.            </p>
              <div className={'text__sliderCard'}>
                {/* TODO */}
                <FeedCard type={'sliderWithoutInfo'} heading={''}/>
              </div>
            </section>
            <section className={'article_centered article__text text'}>
              <p className={'text__subtext'}>
                Синоптик отметил, что обложные дожди будут лить стеной и сменятся во второй половине дня снегопадом с ухудшением видимости до 900 м.
              </p>
              <p className={'text__subtext'}>
                «Это будут самые сильные осадки за этот день за последние 69 лет. Рекорд дня пока принадлежит 1952 году (25,4 мм). Суточный рекорд осадков для всего ноября — 41 мм», — приводят слова специалиста «РИА Новости».            </p>
              <Quote text={'По данным столичных коммунальщиков, высота снежного покрова в городе увеличится на 2 см. Кроме того, ожидается похолодание, что может поспособствовать образованию гололедицы.'}
                     author={'Татьяна Позднякова'}
                     authorsPost={'главный специалист Московского метеобюро'}/>
            </section>
            <section className={'article_centered article__text text'}>
              <p className={'text__subtext'}>
                По данным столичных коммунальщиков, высота снежного покрова в городе увеличится на 2 см. Кроме того, ожидается похолодание, что может поспособствовать образованию гололедицы.
              </p>
              <Share/>
            </section>
          </section>
          <section className={'mainContent__ads ads'}>
            <Advertisement/>
            <Advertisement/>
          </section>
          <section className={'mainContent__comments comments'}>
            <Comments amount={13}/>
          </section>
        </section>
        <aside className={'mainPart__aside aside'}>
          <Advertisement/>
          <Advertisement/>
          <Advertisement/>
          <Newsline heading={'Новое на сайте'}/>
          <Advertisement/>
          <Advertisement/>
          <Advertisement/>
        </aside>
      </section>
      <section className={'bigArticle__ads ads'}>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
      </section>
      <section className={'bigArticle__recommendedArticles recommendedArticles'}>
        <Recommended heading={'Читайте также'}/>
      </section>
    </section>
  )
}

export default BigArticle;