import {FC} from "react";
import "./Popular.scss";
import PopularCard from "./popularCard";

interface PopularProps {

}

const Popular: FC<PopularProps> = ({}) => {
  return (
    <section className={'popular'}>
      <span className={'popular__heading'}>
        Популярное
      </span>
      <div className={'popular__list'}>
        <PopularCard text={'Дачницу из Волгограда оштрафовали за курятник на участке. В четырех номинациях областного конкурса «Лучший по профессии»'}/>
        <PopularCard text={'«Ростелеком» откроет студентам цифровой мир. И пригрозили миллионным штрафом за отказ от прямых договоров'}/>
        <PopularCard text={'Силовики пресекли незаконный сигаретный бизнес в Волгоградской области. В четырех номинациях областного конкурса «Лучший по профессии»'}/>
        <PopularCard text={'Кировский и Советский районы Волгограда соединят новой дорогой за 380 миллионов. И пригрозили миллионным штрафом за отказ от прямых договоров'}/>
        <PopularCard text={'К «экватору» выборов в Волгоградской области явка выросла на 10%. Эксперты прокомментировали итоги выборов в Волгоградской области'}/>
      </div>
    </section>
  )
}

export default Popular;